import { createRouter, createWebHistory } from 'vue-router';
import Home from "../view/Home.vue";
import Apple from "../view/Apple.vue";
import Galaxy from "../view/Galaxy.vue";
const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/iph",
        name: "Apple",
        component: Apple,
    }, {
        path: "/ssph",
        name: "Galaxy",
        component: Galaxy,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router